@media (max-width: 1025px) {
  .curtain {
    min-width: 257px;
    height: 165px;
  }
}

@media (max-width: 682px) {
  .curtain {
    max-width: 360px;
  }
}

@media (max-width: 384px) {
  .curtain {
    width: 255px;
    height: 150px;
  }
}