form {
  display: flex;
  flex-direction: column;
  margin: 0px auto;
  max-width: 65%;
  min-height: 200px;
  margin-right: 100px;


  .inputWrap {
    display: flex;
    justify-content: space-between;

    div {
      width: 32%;
      position: relative;

      .errorMessage {
        position: absolute;
        top: -40px;
        left: 5px;
        width: 100%;
      }

      input {
        height: 30px;
        width: 85%;
        border-radius: 25px;
        background-color: var(--line_color);
        padding: 10px 15px;
        border: none;
        color: var(--dark_text);
        font-size: 17px;

        //Very strange bug with input focus
        &:focus {
          border: none;
          box-shadow: 0px 0px 3px 2px var(--active_yellow);
        }

        &:nth-child(2) {
          margin: 0px 10px;
        }
      }
    }

  }

  .textareaWrap {
    position: relative;

    .errorMessage {
      position: absolute;
      top: 20px;
      left: 5px;
      width: 100%;
    }

    textarea {
      resize: none;
      width: 95%;
      min-height: 150px;
      font-size: 20px;
      padding: 5px;
      border-radius: 25px;
      margin-top: 50px;
      background-color: var(--line_color);
      padding: 10px 15px;
      color: var(--dark_text);
      border: none;
    }
  }

  .buttonWrap {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    button {
      background-image: url(../../../icons/iconmonstr-paper-plane-1.svg);
    }
  }
}

@media (max-width: 962px) {
  form:first-of-type {
    margin: 0px;
    max-width: 100%;

    .inputWrap {
      flex-direction: column;
      align-items: center;

      div {
        width: 90%;
        margin-bottom: 35px;

        .errorMessage {
          top: -25px;
        }

        input {
          width: 95%;

          &:nth-child(2) {
            margin-bottom: 35px;
          }
        }
      }

    }

    .textareaWrap {
      width: 90%;
      margin: 0px auto;

      .errorMessage {
        top: -25px;
      }

      textarea {
        margin-top: 0px;
      }
    }

  }
}

@media (max-width: 562px) {
  form {
    .inputWrap {
      div {
        width: 100%;
      }
    }

    .textareaWrap {
      width: 100%;
    }

    .buttonWrap {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  ;
}